@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  word-wrap: break-word;
  word-break: normal;
  box-sizing: border-box;
  font-family: 'Share Tech Mono', monospace;
  color: rgba(0,0,20, .5);
  text-align: center;

  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
   -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Old versions of Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
          user-select: none; /* Non-prefixed version, currently
                                supported by Chrome, Edge, Opera and Firefox */
}

#body-container {
  height: 100vh;
  width: 100vw;
  background-color: rgb(124,124,124);
  display: flex;
  justify-content: center;
  align-items: center;
}

.s-box-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin: 10px;
  border-radius: 10px;
  min-height: 110px;
  min-width: 200px;
  background-color: rgba(0,60,80,.4);
}

.s-box-container .counter-container {
  width: 50px;
  height: 120px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.s-box-container h1{
  color: white;
  margin: 0px;
}

.s-box-container h3 {
  max-width: 100px;
  font-family: 'Roboto', sans-serif;
  color: rgba(220,220,240,.8);
}

.s-box-container .icon {
  cursor: pointer;
  fill: rgba(0,0,20, .4);
  width: 20px;
  height: 20px;
}

.s-box-container .icon:hover {
  fill: rgba(0,0,20, .6);
  width: 22px;
  height: 22px;
}

.b-box-container {
  box-shadow: inset 0px 0px 0px 1px rgba(0,60,80,.4);
  border-radius: 10px;
  height: auto;
  width: 370px;
  background-color: rgb(190,190,190);
  display: grid;
  grid-template-rows: 1fr 3fr 1fr;
  grid-template-areas: "text" "timer" "controls";
  align-items: center;
}

#timer-label {
  margin-top: 30px;
  margin-bottom: 0px;
  grid-area: "text";
}

#time-left-container {
  grid-area: "timer";
}

#controls {
  grid-area: "controls";
}

.b-box-container #time-left-container h1 {
  color: rgba(0,0,20, .36);
  font-size: 5em;
}


.b-box-container #controls .controls {
  cursor: pointer;
  height: 40px;
  width: 40px;
}

.b-box-container #controls .controls:hover {
  fill: rgba(0,0,20, .8);
  height: 44px;
  width: 44px;
}